<template>
	<transition name="fade">
    <div class="img-view" @click="bigImg">
      <div class="img-layer"></div>
      <div class="img">
        <img :src="imgSrc">
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: "",
    props: [ 'imgSrc' ],
    methods: {
      bigImg() {
        this.$emit('clickit')
      }
    }
  }
</script>

<style scoped>
 .fade-enter-active, .fade-leave-active {
   transition: all .2s linear;
   transform: translate3d(0, 0, 0);
 }
  .fade-enter, .fade-leave-to {
    transform: translate3d(100%, 0, 0);
  }
  .img-view {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .img-view .img-layer {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
 .img-view .img img {
   max-width: 80%;
   display: block;
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
   margin: auto;
   z-index: 1000;
 }
</style>



