<template>
	<div class="store">
    <el-card class="search-wrapper">
      <el-form :model="searchForm" :size="size" ref="searchForm" class="search-form" label-width="100px">
        <el-form-item class="search-form-item" label="样板编号" prop="templateId">
          <el-input v-model="searchForm.templateId"></el-input>
        </el-form-item>
        <el-form-item class="search-form-item" label="样板产区" prop="templateAreas">
          <el-input v-model="searchForm.templateAreas"></el-input>
        </el-form-item>
        <el-form-item class="search-form-item" label="样板分厂" prop="templateFactory">
          <el-input v-model="searchForm.templateFactory" :disabled="(auth === 'factorier')"></el-input>
        </el-form-item>
        <el-form-item class="search-form-item" label="样板存储点" prop="templateStore">
          <el-input v-model="searchForm.templateStore"></el-input>
        </el-form-item>
      </el-form>
      <div class="button-wrapper">
        <el-button :size="size" type="primary" @click="search('searchForm')">搜索</el-button>
        <el-button :size="size" @click="resetForm('searchForm')">重置</el-button>
        <el-button :size="size" type="primary" @click="enterStock" v-has="[api.Stock]">库存新增</el-button>
        <a :href="'http://localhost:8080/templateStock/download?token=' + token" target="_blank" style="margin-left: 10px;">
          <el-button :size="size" type="primary" v-has="[api.Stock]">下载</el-button>
        </a>
      </div>
    </el-card>
    <el-card class="table-wrapper" ref="tableWrapper">
      <el-table :size="size" :data="storeData"
                style="width: 100%" :height="tableHeight" class="table-list">
        <el-table-column prop="templateStock.id" label="库存编号" fixed></el-table-column>
        <el-table-column prop="templateStock.templateId" label="样板编号"></el-table-column>
        <el-table-column prop="templateStock.templateAmount" label="数量（片）"></el-table-column>
        <el-table-column prop="templateStock.templateSuits" label="套数"></el-table-column>
        <el-table-column prop="templateStock.templateAreas" label="产区"></el-table-column>
        <el-table-column prop="templateStock.templateFactory" label="分厂"></el-table-column>
        <el-table-column prop="templateStock.templateStore" label="存放点（货架编号）"></el-table-column>
        <el-table-column label="图片" width="400">
          <template slot-scope="scope" v-if="scope.row.images">
            <img v-for="img in scope.row.images"
                 :src="'http://localhost:8080'+img.pictureUrl"
                 @click="clickImg($event)"
                 alt="" width="70" height="70" style="margin-right: 2px;">
          </template>
        </el-table-column>
        <el-table-column prop="templateStock.templateCtime" width="100" label="上传日期" :formatter="formatterColumn"></el-table-column>
        <el-table-column prop="templateStock.templateMemo" label="备注"></el-table-column>
        <el-table-column width="150" label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              v-if="$_has([api.Stock]) && (auth === 'manager' || factory === scope.row.templateStock.templateFactory)"
              @click="handleEdit(scope.$index, scope.row)">操作</el-button>
            <el-button
              size="mini"
              type="danger"
              v-if="$_has([api.Stock]) && (auth === 'manager' || factory === scope.row.templateStock.templateFactory)"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5, 10, 25, 50]"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="storeDataLength"
        :small="sizesmall"
      >
      </el-pagination>
    </el-card>
    <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
  </div>
</template>

<script>
  import BigImg from './BigImg'
  import moment from 'moment'
  import * as api from 'api/index'
  import * as util from 'assets/js/util'
  import { mapGetters, mapMutations } from 'vuex'
  
  let myMixin = {
    data: function () {
      return { api }
    }
  }
  export default {
    mixins: [myMixin],
    name: "store",
    data() {
      return {
        token: '',
        showImg: false,
        imgSrc: '',
        currentPage: 1,
        pageSize: 10,
        storeDataLength: '',
        sizesmall: false,
        size: 'medium',
        searchForm: {
          templateId: '',
          templateAreas: '',
          templateFactory: '',
          templateStore: ''
        },
        
        storeData: []
      }
    },
    methods: {
      ...mapMutations({
        'setTemplateId': 'SET_TEMPLATE_ID',
        'setTemplateAmount': 'SET_TEMPLATE_AMOUNT'
      }),
      _getFormList() {
        let factory = (this.factory !== '') ? this.factory : this.searchForm.templateFactory
        let form = {
          templateId: this.searchForm.templateId,
          templateAreas: this.searchForm.templateAreas,
          templateFactory: factory,
          templateStore: this.searchForm.templateStore
        }
        api.GetSearchStoreList.r(form).then(({ data }) => {
          this.storeData = data.templateStockList
          this.storeDataLength = data.totalCount
        })
      },
      // 表格方法
      handleEdit(index, row) {
        // console.log(index, row);
        let id = row.templateStock.id
        let amount = row.templateStock.templateAmount
        this.setTemplateId(id)
        this.setTemplateAmount(amount)
        let factory = (this.factory !== '') ? this.factory : this.searchForm.templateFactory
        this.$router.push(`/handle?id=${id}&tid=${this.searchForm.templateId}&areas=${this.searchForm.templateAreas}&factory=${factory}&store=${this.searchForm.templateStore}&currentPage=${this.currentPage}`)
      },
      handleDelete(index, row) {
        this.$confirm('确认删除？', "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: 'info',
          center: true
        }).then(() => {
          api.DeleteStock.r(row.templateStock.id).then(({ data }) => {
            if (data.success === 'SUCCESS') {
              this.$message.success('成功删除库存')
              let factory = (this.factory !== '') ? this.factory : this.searchForm.templateFactory
              let form = {
                templateId: this.searchForm.templateId,
                templateAreas: this.searchForm.templateAreas,
                templateFactory: factory,
                templateStore: this.searchForm.templateStore
              }
              api.GetSearchStoreList.r(form, this.currentPage).then(({ data }) => {
                this.$router.push(`/store?id=${this.searchForm.templateId}&areas=${this.searchForm.templateAreas}&factory=${factory}&store=${this.searchForm.templateStore}&currentPage=1`)
                this.storeData = data.templateStockList
                this.storeDataLength = parseInt(data.totalCount)
              })
            }
          }).catch(err => {
            this.$message.error('未删除，服务器错误')
            console.log(err)
          })
        }).catch(() => {})
        
      },
      enterStock() {
        this.$router.push('/stock')
      },
      // 表单方法
      search(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let factory = (this.factory !== '') ? this.factory : this.searchForm.templateFactory
            let form = {
              templateId: this.searchForm.templateId,
              templateAreas: this.searchForm.templateAreas,
              templateFactory: factory,
              templateStore: this.searchForm.templateStore
            }
            api.GetSearchStoreList.r(form, 1).then(({ data }) => {
              this.$router.push(`/store?id=${this.searchForm.templateId}&areas=${this.searchForm.templateAreas}&factory=${factory}&store=${this.searchForm.templateStore}&currentPage=1`)
              this.currentPage = 1
              this.storeData = data.templateStockList
              this.storeDataLength = parseInt(data.totalCount)
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      formatterColumn(row, column) {
        var date = row.templateStock.templateCtime
        console.log(date)
        if (!date) {
          return ""
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss")
      },
      // 分页器方法
      handleCurrentChange(currentPage) {
        this.currentPage = parseInt(currentPage)
        let factory = (this.auth === 'factorier') ? this.factory : this.searchForm.templateFactory
        let form = {
          templateId: this.searchForm.templateId,
          templateAreas: this.searchForm.templateAreas,
          templateFactory: factory,
          templateStore: this.searchForm.templateStore
        }
        api.GetSearchStoreList.r(form, this.currentPage).then(({ data }) => {
          this.storeData = data.templateStockList
          this.storeDataLength = parseInt(data.totalCount)
        })
        this.$router.push(`/store?id=${this.searchForm.templateId}&areas=${this.searchForm.templateAreas}&factory=${factory}&store=${this.searchForm.templateStore}&currentPage=${currentPage}`)
      },
      // 图片方法
      clickImg(e) {
        this.showImg = true
        this.imgSrc = e.currentTarget.src
      },
      viewImg() {
        this.showImg = false
      }
    },
    computed: {
      ...mapGetters([ 'factory', 'auth' ])
    },
    created() {
      this.token = util.handleSession('token').token
      
      this.currentPage = parseInt(this.$route.query.currentPage) || 1
      
      let factory = (this.auth === 'factorier') ? this.factory : this.$route.query.factory
      this.searchForm.templateId = this.$route.query.id || ''
      this.searchForm.templateAreas = this.$route.query.areas || ''
      this.searchForm.templateStore = this.$route.query.store || ''
      
      let form = {
        templateId: this.searchForm.templateId,
        templateAreas: this.searchForm.templateAreas,
        templateFactory: factory,
        templateStore: this.searchForm.templateStore
      }
      let currentPage = this.currentPage
      api.GetSearchStoreList.r(form, currentPage).then(({ data }) => {
        this.storeData = data.templateStockList
        this.storeDataLength = data.totalCount
        console.log(this.currentPage)
      })
      this.$router.push(`/store?id=${this.searchForm.templateId}&areas=${this.searchForm.templateAreas}&factory=${factory}&store=${this.searchForm.templateStore}&currentPage=${this.currentPage}`)
      // console.log(this.currentPage)
      
      this.size = (window.innerWidth < 767) ? 'mini' : 'medium'
      this.sizesmall = (window.innerWidth < 767) ? true : false
      
    },
    mounted() {
      if (window.innerWidth >= 767) {
        let twHeight = this.$refs.tableWrapper.$el.clientHeight
        this.tableHeight = twHeight - 50
        console.log(this.tableHeight)
      }
    },
    components: {
      BigImg
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "Store.styl"
</style>



